<template>
    <mine-dialog
        title="收入报表"
        :show="show"
        @close="$emit('update:show', false)">
        <div>
            <div>
                <MinePlayground :miner="miner">
                    <span
                        :slot-scope="{ miner }"
                        slot="primary">{{ miner.primaryNum }}位初级</span>
                    <span
                        :slot-scope="{ miner }"
                        slot="intermediate">{{ miner.intermediateNum }}位中级</span>
                    <span
                        :slot-scope="{ miner }"
                        slot="advanced">{{ miner.advancedNum }}位高级</span>
                    <span
                        :slot-scope="{ miner }"
                        slot="rare">{{ miner.rareNum }}位稀有</span>
                </MinePlayground>
            </div>
            <div class="record">
                <div class="coin-count"><span>{{ income.mineCurrency }}</span></div>
                <div class="coin-tip">我已经在想看招募了<span>{{ totalMinerCount }}</span>名矿工不停为我赚钱，已经挖了<span class="total">{{ income.mineCurrency }}</span>矿币，快来和我一起成为家里有矿的人吧！</div> <!-- eslint-disable-line -->
            </div>
            <div
                @click="recordShare"
                class="share" />
        </div>
    </mine-dialog>
</template>
<script>
import {
    shareText,
} from '@/utils/jsApiV2/act';
import MineDialog from './MineDialog';
import MinePlayground from './MinePlayground';
import DialogMixin from './DialogMixin';

export default {
    name: 'MineRecordDialog',
    components: {
        MineDialog,
        MinePlayground,
    },
    mixins: [DialogMixin],
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        miner: {
            type: Object,
            default: () => ({}),
        },
        income: {
            type: Object,
            default: () => ({}),
        },
    },
    computed: {
        totalMinerCount() {
            const count = this.income.primaryNum
                + this.income.intermediateNum
                + this.income.advancedNum
                + this.income.rareNum;

            return count;
        },
    },
    methods: {
        recordShare() {
            // 分享好友
            const url = `https://feed.browser.miui.com/mobile-v2/act/invitation/share?shareId=${encodeURIComponent(this.income.invitationCode)}`;
            const text = `[礼物]来「想看资讯APP」体验当老板的感觉！
[礼物]我招募了${this.totalMinerCount}名矿工给我赚钱
[礼物]截止目前挖到${this.income.mineCurrency}矿币
[耶]不打开app也可以赚钱
[红包]新人登录得最少5元红包
[礼物]填我邀请码【${this.income.invitationCode}】仅剩24小时！快戳：${url}`;

            this.$o2o({ e_c: '收入报表', e_a: '分享', e_n: '收入报表分享' });

            shareText(text, 2);
            this.$emit('update:show', false);
        },
    },
};
</script>
