<template>
    <div
        v-status-bar="false"
        @click.capture="closeMoreMenu"
        class="mine">
        <div class="tools">
            <div class="left-tools">
                <div
                    @click="openRecordDialog"
                    class="record" />
                <div
                    @click="openExchangeDialog"
                    class="exchange"/>
            </div>
            <div class="right-tools">
                <div class="tools-btn">
                    <span
                        @click.capture="toggleMoreMenu"
                        class="btn-more">
                        <span :class="tools.moreMenu">
                            <template v-if="!isFloatWindow()">
                                <span
                                    @click="setDesktopFloatWindow(true)"
                                    class="desktop-tip Hairline Hairline_bottom-outer">
                                    <i class="desktop-tip-icon" />
                                    <span>桌面提醒设置</span>
                                </span>
                            </template>
                            <template v-else>
                                <span
                                    v-if="!isOpenDesktopIcon()"
                                    @click="setDesktopFloatWindow(true)"
                                    class="desktop-tip Hairline Hairline_bottom-outer">
                                    <i class="desktop-tip-icon" />
                                    <span>开启桌面提醒</span>
                                </span>
                                <span
                                    v-else
                                    @click="setDesktopFloatWindow(false)"
                                    class="desktop-tip Hairline Hairline_bottom-outer">
                                    <i class="no-desktop-tip-icon" /><span>关闭桌面提醒</span>
                                </span>
                            </template>
                            <span
                                @click="openRule"
                                class="rule"><i class="rule-icon" /><span>游戏规则</span></span>
                        </span>
                    </span>
                    <span
                        @click="quickGame"
                        class="btn-close" />
                </div>
                <div
                    v-if="!isOpenDesktopIcon() && miner.rareNum < 1"
                    @click="goRecruitRace"
                    class="tools-miner4"/>
            </div>
        </div>
        <div class="underground">
            <div class="tunnal">
                <div class="mine-status">{{ mineStatusLabel }}</div>
                <div
                    ref="tunnalAnimation"
                    class="tunnal-animation"
                />
                <span
                    v-if="subMineEveryChange"
                    :class="animation.tunnalCoinAdd"
                >+{{ subMineEveryChange }}</span>
                <div class="tunnal-coin">
                    <span class="tunnal-total-coin">{{ miner.mineCurrency }}</span>
                    <span class="tunnal-speed">(<span>{{ miner.miningSpeed }}</span>矿币/分钟)</span>
                </div>
                <div class="tunnal-progress">
                    <div :class="animation.speed" />
                </div>
            </div>
            <div class="elevator" />
            <div
                ref="mineCarAnimation"
                :class="animation.mineCar"
            />
            <div :class="animation.truck" />
        </div>
        <div class="cloud-left" />
        <div class="cloud-right" />
        <div class="miner-list">
            <div class="miner-item">
                <div
                    ref="miner1"
                    class="miner1-bg" />
                <div
                    v-if="miner.primaryNum"
                    class="miner-label"><span>{{ miner.primaryNum }}位</span>初级矿工</div>
                <div
                    v-else
                    class="miner-label"><span>等待招募</span></div>
            </div>
            <div class="miner-item">
                <div
                    ref="miner2"
                    class="miner2-bg" />
                <div
                    v-if="miner.intermediateNum"
                    class="miner-label"><span>{{ miner.intermediateNum }}位</span>中级矿工</div>
                <div
                    v-else
                    class="miner-label"><span>等待招募</span></div>
            </div>
            <div class="miner-item">
                <div
                    ref="miner3"
                    class="miner3-bg" />
                <div
                    v-if="miner.advancedNum"
                    class="miner-label"><span>{{ miner.advancedNum }}位</span>高级矿工</div>
                <div
                    v-else
                    class="miner-label"><span>等待招募</span></div>
            </div>
            <div class="miner-item">
                <div
                    ref="miner4"
                    class="miner4-bg" />
                <div
                    v-if="miner.rareNum"
                    class="miner-label"><span>{{ miner.rareNum }}位</span>稀有矿工</div>
                <div
                    v-else
                    class="miner4-label miner-label"><span>等待招募</span></div>
            </div>
        </div>
        <div class="footer">
            <div
                class="recruit-miner"
                @click="readyRecruitMiner(1)">
                <span class="recruit-miner1" />
                <span class="recruit-miner-label">初级矿工</span>
            </div>
            <div
                class="recruit-miner"
                @click="readyRecruitMiner(2)">
                <span class="recruit-miner2" />
                <span class="recruit-miner-label">中级矿工</span>
            </div>
            <div
                class="recruit-miner"
                @click="readyRecruitMiner(3)">
                <span class="recruit-miner3" />
                <span class="recruit-miner-label">高级矿工</span>
            </div>
            <div
                @click="startInspect"
                class="inspect">
                <span class="inspect-bg" />
                <span class="inspect-label">
                    <CountDown
                        @stop="stopInspect"
                        :c-style="{
                            fontSize: '18px',
                            fontFamliy: 'Mitype2018',
                            color: countDownStyleColor,
                            time: {
                                width: 'auto',
                                height: 'auto',
                                margin: '0',
                            }
                        }"
                        :time="miner.countDownTime"
                        :is-show-day="false"
                        :is-stop="true"
                        separator=":"
                    />
                </span>
            </div>
        </div>
        <mine-rule-dialog
            :show.sync="showRuleDialog" />
        <mine-record-dialog
            :show.sync="showRecordDialog"
            :miner="miner"
            :income="income"/>
        <mine-init-dialog
            :show.sync="showInitDialog"
            :recruit-miner-callback="recruitMinerCallback"
            :miner="miner"/>
        <template v-if="showExchangeDialog">
            <mine-exchange-dialog
                :show.sync="showExchangeDialog"
                :exchangeinfo="exchangeinfo"
            />
        </template>
        <mine-expire-dialog
            :show.sync="showExpireDialog"
            :expiredminer="expiredminer"
        />
        <mine-rare-dialog
            :show.sync="showRareDialog"
        />
        <mine-guide
            :next-action="setRecruitRareDialog"
            :show.sync="showGuide" />
        <mine-inspect-dialog
            :show.sync="showMineInspectDialog"
        />
        <mine-recruit-dialog
            :show.sync="showRecruitDialog"
            :miner-type="selectedMinerType" />
        <mine-recruit-rare-dialog
            :show.sync="showRecruitRareDialog"
            :is-fail="recruitRareDialogIsFail"
            @confirm="setDesktopFloatWindow(true)" />
    </div>
</template>
<script>
import lottie from 'lottie-web';
import {
    initRecruitMiner,
    getMinerInfo,
    getMinersIncome,
    getExchangeInfo,
    // getBaseinfo,
    getExpiredminer,
    inspect,
    recruitMiner,
} from '@/api/mining';
import { toast, quitCurrentWebview, getUserAccountInfo } from '@/utils/jsApiV2/client';
import {
    checkFloatWindow,
    packetLogin,
    guideFloatWindow,
    getDesktopFloatWindowStatus,
    setDesktopFloatWindowStatus,
    hasOpenGuideFloatWindow,
} from '@/utils/jsApiV2/game';
import {
    set,
    get,
} from '@/utils/cache';
import {
    addCustomEventListener,
} from '@/utils/event';
import CountDown from '@/components/CountDown';
import GrayMask from '@/components/GrayMask';
import config from '@/config';
import MineRuleDialog from './components/MineRuleDialog';
import MineRecordDialog from './components/MineRecordDialog';
import MineRecruitDialog from './components/MineRecruitDialog';
import MineRecruitRareDialog from './components/MineRecruitRareDialog';
import MineExchangeDialog from './components/MineExchangeDialog';
import MineInitDialog from './components/MineInitDialog';
import MineExpireDialog from './components/MineExpireDialog';
import MineRareDialog from './components/MineRareDialog';
import MineGuide from './components/MineGuide';
import MineInspectDialog from './components/MineInspectDialog';
import {
    minerTypes,
    minerNumKeyMap,
} from './config';

export default {
    name: 'Mining',
    components: {
        CountDown,
        GrayMask,
        MineRuleDialog,
        MineRecordDialog,
        MineRecruitDialog,
        MineExchangeDialog,
        MineRecruitRareDialog,
        MineInitDialog,
        MineExpireDialog,
        MineRareDialog,
        MineGuide,
        MineInspectDialog,
    },
    data() {
        return {
            miner: {
                primaryNum: 0, // 初级矿工数
                intermediateNum: 0, // 中级矿工数
                advancedNum: 0, // 高级矿工数
                rareNum: 0, // 稀有矿工数
                miningSpeed: 0, // 挖矿速度
                mineCurrency: 0, // 已赚矿币
                mineStatus: 0, // 0-偷懒中;1-赚钱中
                countDownTime: 0, // 视察倒计时时间
            },
            animation: {
                mineCar: {
                    'mine-car': true,
                    'mine-car-animation': false,
                },
                truck: {
                    truck: true,
                    'truck-animation': false,
                },
                speed: {
                    'tunnal-progress-inner ': true,
                    'tunnal-progress-inner-animation': false,
                },
                tunnalCoinAdd: {
                    'tunnal-coin-add': true,
                    'tunnal-coin-add-animation': false,
                },
            },
            tools: {
                moreMenu: {
                    'more-menu': true,
                    'more-menu-hidden': true,
                },
            },
            countDownStyleColor: '#fff',
            mineStatusLabel: '休息中',
            minerAnimation: {},
            // dialogKey: '', // rule, record, exchange, init, guide
            showRuleDialog: false,
            showRecordDialog: false,
            showRecruitDialog: false,
            showRecruitRareDialog: false,
            recruitRareDialogIsFail: false,
            income: {},
            showExchangeDialog: false,
            showInitDialog: false,
            showExpireDialog: false,
            showRareDialog: false,
            showGuide: false,
            showMineInspectDialog: false,
            exchangeinfo: {},
            expiredminer: {},
            selectedMinerType: 1,
            subMineEveryChange: 0,
        };
    },
    watch: {
        // eslint-disable-next-line func-names
        'miner.mineStatus': function (val) {
            const isMining = val === 1;

            this.mineStatusLabel = isMining ? '工作中' : '休息中';
            this.animation.mineCar['mine-car-animation'] = isMining;
            this.animation.truck['truck-animation'] = isMining;
            this.animation.speed['tunnal-progress-inner-animation'] = isMining;
            this.animation.tunnalCoinAdd['tunnal-coin-add-animation'] = isMining;

            this.toggleMinerAnimation(isMining);
            this.toggleTunnalAnimation(isMining);
            this.toggleMiner4Animation(isMining);
        },
        // eslint-disable-next-line func-names
        'miner.rareNum': function (val) {
            const isMining = val > 0;

            this.toggleMiner4Animation(isMining);
        },
    },
    methods: {
        hasRareMiner() {
            return this.miner.rareNum > 0;
        },
        isOpenDesktopIcon() {
            return getDesktopFloatWindowStatus();
        },
        isFloatWindow() {
            return checkFloatWindow();
        },
        openRecordDialog() {
            this.$o2o({ e_c: '收入报表icon', e_a: '点击', e_n: '收入报表点击' });

            this.closeMoreMenu();

            getMinersIncome().then((data) => {
                this.income = data;
                this.showRecordDialog = true;
            }).catch(() => {
                toast('查询失败，请稍后再试！');
            });
        },
        openExchangeDialog() {
            this.$o2o({ e_c: '兑换金币icon', e_a: '点击', e_n: '兑换金币icon点击' });

            this.closeMoreMenu();
            getExchangeInfo().then((data) => {
                this.exchangeinfo = data;
                this.showExchangeDialog = true;
            }).catch(() => {
                toast('服务异常，请稍后再试！');
            });
        },
        toggleMoreMenu() {
            this.$o2o({ e_c: '更多icon', e_a: '点击', e_n: '更多icon点击' });
            this.tools.moreMenu['more-menu-hidden'] = !this.tools.moreMenu['more-menu-hidden'];
        },
        closeMoreMenu() {
            this.tools.moreMenu['more-menu-hidden'] = true;
        },
        setDesktopFloatWindow(status) {
            this.closeMoreMenu();
            const is = checkFloatWindow();

            if (is) {
                if (status && this.miner.rareNum) {
                    return;
                }

                // 悬浮通知开启/关闭除稀有矿工的同步操作
                const operateType = status ? 1 : 2;
                const action = status ? '开启' : '关闭';

                recruitMiner({ minerType: 4, minerCount: 1, operateType }).then((data) => {
                    if (data) {
                        this.updateMinerInfo();
                        toast(`悬浮通知${action}成功`);

                        this.setDesktopFloatWindowStatusAdapter(status);
                    } else {
                        toast(`悬浮通知${action}失败，请稍后再试！`);
                    }
                }).catch(() => {
                    toast(`悬浮通知${action}失败，请稍后再试！`);
                });
            } else {
                guideFloatWindow();
            }
        },
        goRecruitRace() {
            this.showRecruitRareDialog = true;
        },
        openRule() {
            this.closeMoreMenu();
            this.showRuleDialog = true;
        },
        quickGame() {
            quitCurrentWebview();
        },
        startInspect() {
            // 可以 reset 视察时间
            // if (this.miner.countDownTime !== 0) {
            //     return;
            // }
            this.$o2o({ e_c: '视察icon', e_a: '点击', e_n: '视察icon点击' });

            inspect().then((data) => {
                if (data === true) {
                    toast('视察成功');
                    this.updateMinerInfo();
                } else {
                    toast('视察失败，请稍后重试！');
                }
            }).catch((e) => {
                console.error(e);
                toast('视察失败，请稍后重试！');
            });
        },
        stopInspect() {
            this.miner.mineStatus = 0;
            this.miner.countDownTime = 0;
            this.countDownStyleColor = '#ec4018';
        },
        updateCoin() {
            if (this.miner.mineStatus) {
                const subMine = Math.floor(this.miner.miningSpeed / 60);

                this.miner.mineCurrency += subMine;
                this.subMineEveryChange = subMine;

                const clientSubMine = Number(get('clientSubMine')) || 0;
                set('clientSubMine', clientSubMine + subMine);
            }

            setTimeout(() => {
                this.updateCoin();
            }, 1000);
        },
        toggleTunnalAnimation(isAnimation) {
            if (isAnimation) {
                this.tunnalAnimationLottie.play();
            } else {
                this.tunnalAnimationLottie.stop();
            }
        },
        tunnalAnimation() {
            this.tunnalAnimationLottie = lottie.loadAnimation({
                container: this.$refs.tunnalAnimation,
                renderer: 'svg',
                loop: true,
                autoplay: false,
                // FIXME: 路径有问题，开发环境和线上环境不统一
                path: `${config.animationDataUrlPerfix}/mining/tunnalAnimation/data.json`,
            });
        },
        toggleMiner4Animation(isAnimation) {
            if (!isAnimation) {
                this.minerAnimation['4'].stop();
            } else {
                this.minerAnimation['4'].play();
            }
        },
        toggleMinerAnimation(isAnimation) {
            let minerNumKey;
            let minerNum;

            minerTypes.forEach((minerType) => {
                minerNumKey = minerNumKeyMap[minerType];
                minerNum = this.miner[minerNumKey];

                if (!isAnimation) {
                    this.minerAnimation[minerType].stop();
                }

                if (minerNum && isAnimation) {
                    this.minerAnimation[minerType].play();
                }
            });
        },
        initAllMinerAnimation() {
            minerTypes.forEach(minerType => this.initMinerAnimation(minerType));
        },
        initMinerAnimation(type) {
            this.minerAnimation[type] = lottie.loadAnimation({
                container: this.$refs[`miner${type}`],
                renderer: 'svg',
                loop: true,
                autoplay: false,
                path: `${config.animationDataUrlPerfix}/mining/miner${type}Animation/data.json`,
            });
        },
        updateMinerInfo(success = () => {}, error = () => {}) {
            getMinerInfo().then((data) => {
                this.$root.hidePlaceholder();

                const clientSubMine = Number(get('clientSubMine')) || 0;

                this.miner.primaryNum = data.primaryNum; // 初级矿工数
                this.miner.intermediateNum = data.intermediateNum; // 中级矿工数
                this.miner.advancedNum = data.advancedNum; // 高级矿工数
                this.miner.rareNum = data.rareNum; // 稀有矿工数
                this.miner.miningSpeed = data.miningSpeed; // 挖矿速度
                this.miner.mineCurrency = data.mineCurrency + clientSubMine; // 已赚矿币
                this.miner.mineStatus = data.mineStatus; // 0-偷懒中;1-赚钱中
                this.miner.countDownTime = data.inspectRemainingTime;
                this.countDownStyleColor = this.miner.countDownTime === 0 ? '#ec4018' : '#fff';

                set('clientSubMine', 0);
                success();
            }).catch((e) => {
                console.error(e);
                error();
            });
        },
        updateMiner() {
            const callback = () => {
                const time = 1000 * 60 * 60;

                setTimeout(() => {
                    this.updateMinerInfo();
                }, time);
            };

            this.updateMinerInfo(callback, callback);
        },
        recruitMinerCallback() {
            this.updateMiner();
            this.updateCoin();
        },
        expiredMinersTip() {
            getExpiredminer().then((data) => {
                if (data
                    && (data.expirePrimaryNum
                        || data.expireIntermediateNum
                        || data.expireAdvancedNum
                    )
                ) {
                    this.showExpireDialog = true;
                    this.expiredminer = data;
                    this.miner.miningSpeed = data.afterMiningSpeed;
                }
            }).catch((e) => {
                console.error(e);
            });
        },
        focusUpdate() {
            this.expiredMinersTip();

            const is = hasOpenGuideFloatWindow();
            const isAuth = checkFloatWindow();

            if (is) {
                if (isAuth) {
                    if (this.miner.rareNum) {
                        toast('桌面悬浮窗开启成功，获得 1 位稀有矿工');
                        return this.setDesktopFloatWindowStatusAdapter(true);
                    }

                    return recruitMiner({ minerType: 4, minerCount: 1, operateType: 1 })
                        .then((data) => {
                            if (data) {
                                this.setDesktopFloatWindowStatusAdapter(true);
                                this.updateMinerInfo();
                                this.showRareDialog = true;
                            } else {
                                toast('稀有矿工申请失败，请稍后再试！');
                            }
                        }).catch(() => {
                            toast('稀有矿工申请失败，请稍后再试！');
                        });
                }

                // TODO: 残忍拒绝；弹窗展示一次
                if (get('isShowRecruitRareDialog') !== '1') {
                    set('isShowRecruitRareDialog', '1');
                    this.recruitRareDialogIsFail = true;
                    this.showRecruitRareDialog = true;
                } else {
                    toast('桌面提醒设置失败');
                }

                return 1;
            }

            return 1;
        },
        readyRecruitMiner(type) {
            this.selectedMinerType = type;
            this.showRecruitDialog = true;
        },
        setRecruitRareDialog() {
            // 招募稀有矿工
            this.recruitRareDialogIsFail = false;
            this.showRecruitRareDialog = true;
        },
        setDesktopFloatWindowStatusAdapter(status) {
            const data = {
                e_c: '桌面悬浮窗',
                e_a: status ? '开启成功' : '关闭',
                e_n: status ? '桌面悬浮窗开启成功' : '桌面悬浮窗关闭',
            };

            this.$o2o(data);
            setDesktopFloatWindowStatus(status);
        },
    },
    beforeRouteEnter(_, __, next) {
        const user = getUserAccountInfo();
        window.minerBeforeRouteEnterNext = next;

        if (!user.user_id) {
            packetLogin('window.minerBeforeRouteEnterNext');
        } else {
            next();
        }
    },
    created() {
        const ref = this.$route.query.o2o;

        if (ref === 'desktop') {
            this.$o2o({ e_c: '桌面悬浮窗', e_a: '桌面悬浮窗调起', e_n: '桌面悬浮窗调起app' });
        }

        this.$o2o({ e_c: '游戏页面', e_a: '曝光', e_n: '游戏页面曝光' });
        initRecruitMiner().then((data) => {
            // TODO: 新手引导
            if (data && data.firstMiningStatus === 0) {
                this.showGuide = true;
            } else {
                if (!get('MineInspectDialog')) {
                    this.showMineInspectDialog = true;
                    set('MineInspectDialog', true);
                }
                addCustomEventListener('focus', () => {
                    const is = getDesktopFloatWindowStatus();
                    const isAuth = checkFloatWindow();

                    if (isAuth && is) {
                        recruitMiner({ minerType: 4, minerCount: 1, operateType: 1 }).then(() => {
                            this.updateMinerInfo();
                        });
                    } else {
                        recruitMiner({ minerType: 4, minerCount: 1, operateType: 2 }).then(() => {
                            this.updateMinerInfo();
                        });
                    }
                });
            }

            this.recruitMinerCallback();
            this.expiredMinersTip();

            addCustomEventListener('focus', () => {
                this.focusUpdate();
            });

            this.$root.$on('updateMiner', () => {
                this.updateMinerInfo();
            });
        }).catch((e) => {
            this.$error();
            console.error(e);
        });
    },
    mounted() {
        this.initAllMinerAnimation();
        this.tunnalAnimation();
    },
};
</script>
<style scoped>
.mine {
    width: 100%;
    height: 100%;
    background-image: url(./image/bg.webp);
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: left bottom;
    position: relative;
    overflow: hidden;
}

.tools {
    width: 100%;
    margin-top: 16px;
    padding: 0 7px 0 16.666666px;
}

.tools {
    display: flex;
    flex-direction: row;
}

.left-tools {
    display: flex;
    flex-direction: row;
    flex: 1;
}

.record,
.exchange {
    position: relative;
    z-index: 100;
    width: 50px;
    height: 56.333333px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.record {
    background-image: url(./image/recordStats.webp);
    margin-right: 13.333333px;
}

.exchange {
    background-image: url(./image/exchangeCoin.webp);
}

.tools-btn,
.tools-miner4,
.tools-miner4::after {
    background-repeat: no-repeat;
    background-size: 100% auto;
    z-index: 100;
}

@keyframes heartBeatSmall {
    0% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }

    14% {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
    }

    28% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }

    42% {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
    }

    70% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

.tools-miner4::after {
    animation-name: heartBeatSmall;
    animation-iteration-count: infinite;
    animation-duration: 1.3s;
    animation-timing-function: ease-in-out;
