<template>
    <mine-dialog
        title="矿币提现"
        :show="show"
        @close="$emit('update:show', false)">
        <div>
            <div class="rate">
                <span>{{ exchangeinfo.exchangeMultiple }}</span>矿币=<span>1</span>金币
            </div>
            <div class="item-input">
                <mine-number-input
                    :number="exchangeinfo.mineCurrency"
                    label="当前矿币数量" />
            </div>
            <div class="item-input">
                <mine-number-input
                    :step="exchangeinfo.exchangeMultiple"
                    :limit="maxChangeCount"
                    v-model="exchangeMineCoin"
                    label="可兑换数" />
                <div class="tip">(*当前矿币只能取证兑换)</div>
            </div>
            <div class="item-input">
                <mine-number-input
                    :number="exchangeCoin"
                    theme="red"
                    label="兑换金币" />
            </div>
            <div
                @click="displayMiningAd"
                class="btn"/>
        </div>
    </mine-dialog>
</template>
<script>
import config from '@/config';
import {
    exchange,
} from '@/api/mining';
import {
    toast,
} from '@/utils/jsApiV2/client';
import {
    loadMiningAd,
    displayMiningAd } from '@/utils/jsApiV2/game';
import MineDialog from './MineDialog';
import DialogMixin from './DialogMixin';
import MineNumberInput from './MineNumberInput';

const submitExchange = 'submitExchange';
const handleExchangeResult = 'handleExchangeResult';

export default {
    name: 'MineExchangeDialog',
    components: {
        MineDialog,
        MineNumberInput,
    },
    mixins: [DialogMixin],
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        exchangeinfo: {
            type: Object,
            default: () => ({}),
        },
    },
    watch: {
        exchangeinfo(val) {
            this.exchangeMineCoin = val.maxChangeCount;
            this.maxChangeCount = val.maxChangeCount;
        },
        exchangeMineCoin() {
            this.exchangeCoin = Math.floor(
                this.exchangeMineCoin / this.exchangeinfo.exchangeMultiple,
            );
        },
    },
    data() {
        return {
            exchangeCoin: this.exchangeinfo.coin,
            maxChangeCount: this.exchangeinfo.maxChangeCount,
            exchangeMineCoin: this.exchangeinfo.maxChangeCount,
            exchangeResult: '',
        };
    },
    mounted() {
        window.submitExchange = this.submitExchange;
        window.handleExchangeResult = this.handleExchangeResult;
        loadMiningAd(
            submitExchange,
            handleExchangeResult);
    },
    methods: {
        displayMiningAd() {
            // 客户端新增方法40920发版
            if (config.versionCode < 40920) {
                toast('兑换失败，请升级到新版本重试！');
                return;
            }
            if (this.exchangeCoin === 0) {
                toast('兑换金币需要大于 0');
                return;
            }
            // 调用客户端方法
            displayMiningAd();
        },
        handleExchangeResult() {
            setTimeout(() => {
                if (this.exchangeResult === true) {
                    this.emitUpdateMiner({ exchange: this.exchangeMineCoin });
                    this.$emit('update:show', false);

                    toast('兑换成功');
                } else {
                    toast('兑换失败，请稍后在试！');
                }
            }, 500);
        },
        submitExchange() {
            this.$o2o({ e_c: '矿币提现',
                e_a: '确认兑换',
                e_n: '矿币兑换',
                e_x: {
                    total: this.exchangeMineCoin,
                },
            });
            return exchange({
                mineCurrency: this.exchangeinfo.mineCurrency,
                changeCount: this.exchangeMineCoin,
            }).then((data) => {
                this.exchangeResult = data;
            }).catch(() => {
                toast('兑换失败，请稍后在试！');
            });
        },
    },
};
</script>
