<template>
    <mine-dialog
        :title="title"
        :show="show"
        @close="closeDialog">
        <mine-info
            :header="header"
            :image-url="imageUrl"
            :current-num="currentNum"
            :mining-speed="miningSpeed"
            :max="max"
            :expire-day="expireDay"
            :available="available" />
        <mine-number-input
            v-model="minerCount"
            :limit="miner.recruitNum" />
        <div class="tip">
            <div :class="['cost-tip', { error: !canAfford } ]"><span>{{ costTip }}</span></div>
            <div :class="['limit-tip', { error: !canRecruit }]"><span>{{ limitTip }}</span></div>
        </div>
        <div
            class="recruit-btn"
            @click="handleRecruit" />
    </mine-dialog>
</template>
<script>
import MineNumberInput from './MineNumberInput';
import MineRecruitDialogMixin from './MineRecruitDialogMixin';
import { minerTypeMap } from '../config';

export default {
    name: 'MineRecruitDialog',
    mixins: [MineRecruitDialogMixin],
    components: {
        MineNumberInput,
    },
    data() {
        return {
            minerCount: 1,
        };
    },
    computed: {
        currentNum() {
            return this.miner.currentNum || 0;
        },
        expireDay() {
            return this.miner.expireDay || 0;
        },
        title() {
            return `招募${minerTypeMap[this.minerType]}`;
        },
        canAfford() {
            return this.balance >= this.cost;
        },
        canRecruit() {
            return this.currentNum + this.minerCount <= this.max;
        },
        consume() {
            return this.miner.consume || 0;
        },
        cost() {
            return this.consume * this.minerCount;
        },
        balance() {
            return this.miner.available || 0;
        },
        available() {
            let res = '';

            switch (this.minerType) {
            case 1:
                res = `可用阅读时长:${this.balance}分钟`;
                break;
            case 2:
                res = `可用金币${this.balance}`;
                break;
            case 3:
                res = `可用好友${this.balance}人`;
                break;
            default:
                break;
            }

            return res;
        },
        costTip() {
            let res = '';

            switch (this.minerType) {
            case 1:
                res = `消耗时长${this.cost}分钟`;
                break;
            case 2:
                res = `消耗金币${this.cost}`;
                break;
            case 3:
                res = `消耗好友${this.cost}人`;
                break;
            default:
                break;
            }

            return res;
        },
        limitTip() {
            return `当前上限${this.max}人`;
        },
        header() {
            let res = '';

            switch (this.minerType) {
            case 1:
                res = `消耗阅读时长${this.consume}分钟，可招募一个初级矿工！`;
                break;
            case 2:
                res = `消耗${this.consume}想看金币，可招募一个中级矿工！`;
                break;
            case 3:
                res = `邀请${this.consume}个好友，可招募一个高级矿工！`;
                break;
            default:
                break;
            }

            return res;
        },
    },
    watch: {
        show: {
            handler(newVal) {
                if (newVal) {
                    this.getRecruitInfo(this.minerType);
                } else {
                    this.minerCount = 1;
                }
            },
            immediate: true,
        },
    },
};
</script>
