    content: "!";
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 10px;
    height: 10px;
    font-size: 9px;
    color: #fff;
    margin-right: 2px;
    background-color: #ff4723;
    border-radius: 999px;
}

.tip .error {
    color: #ff4723;
}

.recruit-btn {
    width: 152px;
