<template>
    <div :class="['mine-number-input', theme]">
        <div
            v-if="label"
            class="label">
            <span>{{ label }}</span>
        </div>
        <div
            v-if="number !== null"
            class="number-display">{{ number }}</div>
        <div
            v-else
            class="number-input">
            <div
                :class="['sub', 'btn', { disabled: isSubDisabled }]"
                @click="sub" />
            <div class="number">{{ value }}</div>
            <div
                :class="['add', 'btn', { disabled: isAddDisabled }]"
                @click="add" />
        </div>
    </div>
</template>
<script>
export default {
    name: 'MineNumberInput',
    props: {
        value: {
            type: Number,
            default: 0,
        },
        number: {
            type: Number,
            default: null,
        },
        label: {
            type: String,
            default: '',
        },
        step: {
            type: Number,
            default: 1,
        },
        limit: {
            type: Number,
            default: 10,
        },
        theme: {
            type: String,
            default: '',
        },
    },
    computed: {
        isSubDisabled() {
            return this.value < this.step;
        },
        isAddDisabled() {
            return (this.value + this.step) > this.limit;
        },
    },
    methods: {
        add() {
            if (!this.isAddDisabled) {
                this.$emit('input', this.value + this.step);
            }
        },
        sub() {
            if (!this.isSubDisabled) {
                this.$emit('input', this.value - this.step);
            }
        },
    },
};
</script>
