import {
    isTakeOverBackByWeb,
} from '@/utils/jsApiV2/client';
import {
    addCustomEventListener,
    removeCustomEventListener,
} from '@/utils/event';

export default {
    watch: {
        show(val) {
            if (val) {
                isTakeOverBackByWeb(true);
                addCustomEventListener('backKey', this.close);
            } else {
                isTakeOverBackByWeb(false);
                removeCustomEventListener('backKey', this.close);
            }
        },
    },
    methods: {
        close() {
            this.$emit('update:show', false);
        },
        emitUpdateMiner(data = {}) {
            this.$root.$emit('updateMiner', data);
        },
    },
};
