<template>
    <mine-dialog
        title="想看矿场游戏规则"
        :show="show"
        @close="$emit('update:show', false)">
        <div
            class="mine-rule">
            <div
                v-for="(section, index) in sections"
                :key="index"
                class="section">
                <div class="title">
                    <span>{{ section.title }}</span>
                </div>
                <div class="rules">
                    <div
                        v-for="rule in section.rules"
                        :key="rule"
                        class="rule">
                        {{ rule }}
                    </div>
                </div>
            </div>
        </div>
    </mine-dialog>
</template>
<script>
import { getConfigByKey } from '@/api/common';
import {
    apolloMiningRuleKey,
    defaultRules,
} from '../config';
import MineDialog from './MineDialog';
import DialogMixin from './DialogMixin';

export default {
    name: 'MineRuleDialog',
    components: {
        MineDialog,
    },
    mixins: [DialogMixin],
    props: {
        show: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            sections: [],
        };
    },
    created() {
        this.getRules();
    },
    watch: {
        show(newVal) {
            if (newVal) {
                this.$o2o({ e_c: '游戏规则弹窗', e_a: '曝光', e_n: '游戏规则弹窗曝光' });
            }
        },
    },
    methods: {
        getRules() {
            getConfigByKey({
                key: apolloMiningRuleKey,
            })
                .then((res) => {
                    try {
                        this.sections = res.data.content;
                    } catch (_) {
                        this.sections = defaultRules;
                    }
                })
                .catch((e) => {
                    console.error(e);
                });
        },
    },
};
</script>
