.mine-rule {
    height: 368px;
    overflow-y: auto;
}

.section + .section {
    margin-top: 16px;
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    text-align: center;
    color: #fe4817;
}

.title::before,
.title::after {
    content: "";
    width: 6px;
    height: 6px;
    background-color: #fe4817;
    transform: rotateZ(45deg);
}

.title span {
    margin: 0 3px;
    padding: 0 2px;
    box-shadow: inset 0 -5px #fbcdc1;
}

.rules {
    margin-top: 9px;
    font-size: 13px;
    line-height: 19px;
    color: #297fff;
}
</style>
