import {
    getRecruitInfo,
    recruitMiner,
} from '@/api/mining';
import {
    toast,
    getReadTime,
} from '@/utils/jsApiV2/client';
import MineDialog from './MineDialog';
import MineInfo from './MineInfo';
import DialogMixin from './DialogMixin';
import { minerTypeMap } from '../config';

export default {
    mixins: [DialogMixin],
    components: {
        MineDialog,
        MineInfo,
    },
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        minerType: {
            type: Number,
            default: 1,
        },
    },
    data() {
        return {
            miner: {},
        };
    },
    computed: {
        imageUrl() {
            return this.miner.imageUrl || '';
        },
        miningSpeed() {
            return this.miner.miningSpeed || 0;
        },
        max() {
            return this.miner.max || 0;
        },
        readingTime() {
            // // TODO: just for test
            // const time = 40;
            // console.log(getReadTime());
            const time = Math.floor((getReadTime() || 0) / 60000);

            return time;
        },
    },
    methods: {
        getRecruitInfo(minerType) {
            getRecruitInfo({
                minerType,
                ...(minerType === 1 && {
                    readingTime: this.readingTime, // 当矿工类型=1时传readingTime，便于计算最大可兑换初级矿工数
                }),
            })
                .then((res) => {
                    this.miner = {
                        ...this.miner,
                        ...res,
                    };
                })
                .catch(() => {
                    setTimeout(() => {
                        this.closeDialog();
                    }, 1000);

                    toast('获取矿工信息失败，请重试');
                });
        },
        handleRecruit() {
            this.$o2o({
                e_c: '招募矿工',
                e_a: '点击',
                e_n: '招募矿工点击',
                e_x: {
                    string_type: minerTypeMap[this.minerType].substring(0, 2),
                },
            });

            if (!this.canAfford
                || !this.canRecruit
                || !this.minerCount) {
                const warning = !this.minerCount
                    ? '招募人数需大于0'
                    : '不符合招募条件';

                return toast(warning);
            }

            return recruitMiner({
                minerType: this.minerType,
                minerCount: this.minerCount,
                operateType: 1,
            })
                .then((res) => {
                    if (res) {
                        toast('招募成功');

                        this.$o2o({
                            e_c: '招募矿工',
                            e_a: '点击',
                            e_n: '招募矿工成功',
                            e_x: {
                                string_type: minerTypeMap[this.minerType].substring(0, 2),
                            },
                        });
                        this.emitUpdateMiner();

                        return this.closeDialog();
                    }

                    return Promise.reject();
                })
                .catch(() => {
                    toast('招募失败，请重试');
                });
        },
        closeDialog() {
            this.$emit('update:show', false);
        },
    },
};
