<template>
    <gray-mask :show="show">
        <div
            v-show="guideIndex === 0"
            class="guide"
            @click="nextStep(1)">
            <div class="guide-0">
                <div class="window-0"><div class="guide-pointer guide0-pointer" /></div>
                <div class="arrow" />
                <mine-guide-info title="第一步">
                    <div>点击<span>兑换金币</span>立即取钱</div>
                </mine-guide-info>
            </div>
        </div>
        <div
            v-show="guideIndex === 1"
            class="guide"
            @click="nextStep(2)">
            <div class="guide-1">
                <mine-guide-info title="第二步">
                    <div>老板你需要经常视察工作，不然<br>矿工们会怠慢工作哟！快点击<br><span>视察</span>让他们为你赚钱吧！</div>
                </mine-guide-info>
                <div class="arrow down" />
                <div class="window-1"><div class="guide-pointer guide1-pointer" /></div>
            </div>
        </div>
        <div
            v-show="guideIndex === 2"
            class="guide"
            @click="nextStep()">
            <div class="guide-2">
                <div class="window-2"><div class="guide-pointer guide2-pointer" /></div>
                <div class="arrow" />
                <mine-guide-info title="第三步">
                    <div>这里显示你的矿工速度及当前余额，<br>挖矿速度可以通过<span>招募矿工</span>提升。</div>
                </mine-guide-info>
            </div>
        </div>
    </gray-mask>
</template>
<script>
import GrayMask from '@/components/GrayMask';
import MineGuideInfo from './MineGuideInfo';

export default {
    name: 'MineGuide',
    components: {
        GrayMask,
        MineGuideInfo,
    },
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        nextAction: {
            type: Function,
            default: () => {},
        },
    },
    data() {
        return {
            guideIndex: 0,
        };
    },
    methods: {
        nextStep(next) {
            if (next) {
                this.guideIndex = next;
            } else {
                this.$emit('update:show', false);
                this.nextAction();
            }
        },
    },
};
</script>
