<template>
    <mine-dialog
        title="恭喜老板 开业大吉"
        :show="show"
        @close="$emit('update:show', false)">
        <div>
            <div class="title">想看小助手为您送来开业贺礼，领取后矿工就能开始挖矿赚钱了，矿币可以随时兑换成金币哦！</div>
            <div class="miner">
                <MinePlayground :miner="miner">
                    <span slot="primary">初级矿工*<span class="count">6</span></span>
                    <span slot="intermediate">中级矿工*<span class="count">3</span></span>
                    <span slot="advanced">高级矿工*<span class="count">1</span></span>
                </MinePlayground>
            </div>
            <div
                @click="getInitMiner"
                class="btn" />
        </div>
    </mine-dialog>
</template>
<script>
import { initRecruitMiner } from '@/api/mining';
import {
    toast,
} from '@/utils/jsApiV2/client';
import MineDialog from './MineDialog';
import MinePlayground from './MinePlayground';
import DialogMixin from './DialogMixin';

export default {
    name: 'MineInitDialog',
    components: {
        MineDialog,
        MinePlayground,
    },
    mixins: [DialogMixin],
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        recruitMinerCallback: {
            type: Function,
            required: true,
            default: () => {},
        },
    },
    data() {
        return {
            miner: {
                primaryNum: 6,
                intermediateNum: 2,
                advancedNum: 1,
            },
        };
    },
    methods: {
        getInitMiner() {
            initRecruitMiner().then((data) => {
                if (data.primaryNum !== 0) {
                    toast('领取成功');
                    this.$emit('update:show', false);
                    this.recruitMinerCallback();
                } else {
                    toast('领取失败，请稍后重试！');
                }
            }).catch(() => {
                toast('领取失败，请稍后重试！');
            });
        },
    },
};
</script>
