<template>
    <transition name="fade">
        <div
            v-if="show"
            class="mine-dialog">
            <div class="content">
                <div class="title">{{ title }}</div>
                <slot />
            </div>
            <div
                class="close"
                @click="$emit('close')" />
        </div>
    </transition>
</template>
<script>
export default {
    name: 'MineDialog',
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        title: {
            type: String,
            default: '',
        },
    },
    watch: {
        show(newVal) {
            // 防止滑动穿透
            if (newVal === true) {
                const cssStr = 'overflow-y: hidden;';
                document.getElementsByTagName('html')[0].style.cssText = cssStr;
                document.body.style.cssText = cssStr;
            } else {
                const cssStr = 'overflow-y: auto;';
                document.getElementsByTagName('html')[0].style.cssText = cssStr;
                document.body.style.cssText = cssStr;
            }
        },
    },
};
</script>
