<template>
    <mine-dialog
        :title="title"
        :show="show"
        @close="closeDialog">
        <mine-info
            :is-rare="true"
            :header="header"
            :image-url="imageUrl"
            :mining-speed="miningSpeed"
            :max="max" />
        <div :class="['btns', { fail: isFail }]">
            <div
                class="cancel"
                @click="handleCancel" />
            <div
                class="confirm"
                @click="handleConfirm" />
        </div>
    </mine-dialog>
</template>
<script>
import MineRecruitDialogMixin from './MineRecruitDialogMixin';

export default {
    name: 'MineRecruitRareDialog',
    mixins: [MineRecruitDialogMixin],
    data() {
        return {
            rareMinerType: 4,
        };
    },
    props: {
        isFail: {
            type: Boolean,
            default: false,
        },
    },
    computed: {
        title() {
            return this.isFail ? '解锁失败' : '恭喜老板 开张大吉';
        },
        header() {
            return this.isFail
                ? '还差一步就能解锁稀有矿工，继续去开通桌面提醒！2亿小目标立即实现！'
                : '想看额外送您一个稀有矿工，<span>开通桌面提醒就可以获得</span>，永久有效赚钱必备！';
        },
    },
    watch: {
        show: {
            handler(newVal, oldVal) {
                if (newVal) {
                    const data = {
                        e_c: this.isFail ? '解锁失败弹窗' : '解锁稀有矿工弹窗',
                        e_a: '曝光',
                        e_n: this.isFail ? '解锁失败弹窗曝光' : '解锁稀有矿工弹窗曝光',
                    };

                    this.$o2o(data);
                    this.getRecruitInfo(this.rareMinerType);
                }

                if (!newVal && oldVal) {
                    const data = {
                        e_c: this.isFail ? '解锁失败弹窗' : '解锁稀有矿工弹窗',
                        e_a: '关闭',
                        e_n: this.isFail ? '解锁失败弹窗关闭' : '解锁稀有矿工弹窗关闭',
                    };
                    this.$o2o(data);
                }
            },
            immediate: true,
        },
    },
    methods: {
        handleConfirm() {
            const data1 = {
                e_c: this.isFail ? '解锁失败弹窗' : '解锁稀有矿工弹窗',
                e_a: '点击',
                e_n: this.isFail ? '解锁失败弹窗点击' : '解锁稀有矿工弹窗点击',
                e_x: {
                    name: this.isFail ? '继续解锁' : '确定招募',
                },
            };
            this.$o2o(data1);

            this.$emit('confirm');
            this.$emit('update:show', false);
        },
        handleCancel() {
            const data = {
                e_c: this.isFail ? '解锁失败弹窗' : '解锁稀有矿工弹窗',
                e_a: '点击',
                e_n: this.isFail ? '解锁失败弹窗点击' : '解锁稀有矿工弹窗点击',
                e_x: {
                    name: this.isFail ? '残忍放弃' : '放弃机会',
                },
            };

            this.$o2o(data);
            this.closeDialog();
        },
    },
};
</script>
<style scoped>
.btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 45px;
}

.btns div {
    width: 117px;
    height: 34px;
}

.btns .cancel {
    background: url(./image/cancel-0.webp) no-repeat;
    background-size: 100% 100%;
}

.btns .confirm {
    background: url(./image/confirm-0.webp) no-repeat;
    background-size: 100% 100%;
}

.btns.fail .cancel {
    background: url(./image/cancel-1.webp) no-repeat;
    background-size: 100% 100%;
}

.btns.fail .confirm {
    background: url(./image/confirm-1.webp) no-repeat;
    background-size: 100% 100%;
}
</style>
